<template>
  <div>
    <!-- desktop -->
    <v-app-bar
      class="px-0 mx-0 teal darken-1 hidden-sm-and-down"
      height="80%"
      style="
        background-color: #74ebd5;
        background-image: linear-gradient(90deg, #5edbdb 5%, #008080 100%);
      "
    >
      <v-img
        class="ml-10 mr-5 align-end"
        max-height="120px"
        max-width="120px"
        src="../assets/img/logo_one.png"
      ></v-img>
      <v-divider class="mx-6" color="white" vertical inset></v-divider>
      <v-card-actions class="">
        <h1 class="mt-3 white--text font-weight-bold">One Smart Access</h1>
      </v-card-actions>
      <v-spacer></v-spacer>
      <v-btn
        @click="requestDevice()"
        color="white"
        large
        outlined
        height="55px"
        style="border-radius: 15px;"
        class="ma-2 white--text"
      >
        <v-icon class="mr-3" large color="white">mdi-refresh-circle</v-icon>Scan
        Device
      </v-btn>

      <v-btn
        @click="logout()"
        color="white"
        large
        outlined
        height="55px"
        style="border-radius: 15px;"
        class="ma-2 white--text"
      >
        <v-icon class="mr-3" large color="white">mdi-arrow-right-circle</v-icon
        >Logout
      </v-btn>
    </v-app-bar>

    <!-- mobile -->
    <v-app-bar
      class="px-0 mx-0 teal darken-1 hidden-md-and-up"
      height="70%"
      style="
        background-color: #74ebd5;
        background-image: linear-gradient(90deg, #5edbdb 5%, #008080 100%);
      "
    >
      <v-img
        class="ml-2 mr-3 align-end"
        max-height="70px"
        max-width="70px"
        src="../assets/img/logo_one.png"
      ></v-img>
      <v-divider class="mx-1" color="white" vertical inset></v-divider>
      <v-card-actions class="">
        <h3 class="mt-3 white--text font-weight-light">One Smart Access</h3>
      </v-card-actions>
      <v-spacer></v-spacer>
      <v-avatar size="34px">
      <img
        src="https://cdn.vuetifyjs.com/images/john.jpg"
        alt="John"
      >
    </v-avatar>

      <v-icon class="ml-2" size="34px" color="white" @click="logout()"
        >mdi-arrow-right-thin-circle-outline</v-icon
      >
    </v-app-bar>

    <router-view></router-view>
  </div>
</template>

<script>
import Swal from "sweetalert2";
export default {
  components: {},
  data: () => ({
    fab: false,
    direction: "top",
    drawer: false,
    user_login: "",
    menuVisible: false,
    transition: "slide-y-reverse-transition",
  }),
  created() {
    // this.get_user_login();
  },
  methods: {
    requestDevice() {
      console.log("Requesting any Bluetooth Device...");

      this.clean_state();
      if (this.$store.state.check_state != "") {
        this.$store.state.name_device.gatt.disconnect();
      }
      return navigator.bluetooth
        .requestDevice({
          acceptAllDevices: true,
          optionalServices: ["1fb32be3-958f-463c-893e-f9e33911f256"],
        })
        .then((device) => {
          var state = "show";
          this.bluetoothDevice = device;
          this.$store.commit("data_device", device);
          this.$store.commit("check_state", state);

          var status = "connect";
          this.$store.commit("status", status);

          console.log(state);
        });
    },
    clean_state() {
      var state = "";
      this.$store.commit("check_state", state);
      // console.log(state)
    },
    logout() {
      Swal.fire({
        title: "Are you sure?",
        text: "Logout !!",
        icon: "info",
        showCancelButton: true,
        confirmButtonColor: "#d33",
        cancelButtonColor: "#3085d6",
        confirmButtonText: "Yes!",
      }).then((result) => {
        if (result.isConfirmed) {
          this.save_user_login();
          this.$router.push("/login");
        }
      });
    },
    save_user_login() {
      var user_login = "";
      window.localStorage.setItem(
        "user_login_oneaccess",
        JSON.stringify(user_login)
      );
    },
  },
};
</script>

<style lang="scss" scoped>
#create .v-speed-dial {
  position: absolute;
}

#create .v-btn--floating {
  position: relative;
}
.md-app {
  min-height: 350px;
  border: 1px solid rgba(#000, 0.12);
}

// Demo purposes only
.md-drawer {
  width: 230px;
  max-width: calc(100vw - 125px);
}

.underline {
  text-decoration: underline;
}

.backgroud_Plug {
  background-color: #ffd9d8;
}

.border {
  border-color: #ffd9d8;
}

.csslogout {
  align-content: start;
  justify-content: space-between;
}
</style>