import Vue from 'vue'
import VueRouter from 'vue-router'
import Navbar from '../views/Navbar'

//main
const HomePage = () => import("../views/HomePage.vue")
const login = () => import("@/views/Login.vue")

//component views
const CalendarMeeting = () => import("@/views/CalendarMeeting.vue")
const JoinCalendarMeeting = () => import("@/views/JoinCalendarMeeting.vue")
const SchedulePerson = () => import("@/views/SchedulePerson.vue")


// const Home = () => import("../views/Home.vue")

Vue.use(VueRouter)

const routes = [
  { path: '/login',name: "login", component: login },

  {
    path: "/",
    name: "Navbar",
    component: Navbar,
    children: [
      {
        path: "/HomePage",
        name: "HomePage",
        component: HomePage
      },
      {
        path: "/CalendarMeeting",
        name: "CalendarMeeting",
        component: CalendarMeeting
      },
      {
        path: "/JoinCalendarMeeting",
        name: "JoinCalendarMeeting",
        component: JoinCalendarMeeting
      },
      {
        path: "/SchedulePerson",
        name: "SchedulePerson",
        component: SchedulePerson
      },
      { path: '/*', redirect: '/login' },
    ]
    
  }
]
const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
});

export default router;

 


