import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    name_device:{},
    check_state:"",
    status:"connect",
  },
  mutations: {
    data_device(state,n){
      state.name_device = n
    },
    check_state(state,n){
      state.check_state = n
    },
    status(state,n){
      state.status = n
    }
  },
  actions: {
  },
  modules: {
  }
})
